.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  max-width: 480px;
  display: flex;
  flex-direction: column;
}

.app-header, .app-footer {
  margin: 0;
  height: 32px;
  padding: 12px;
  font-size: 26px;
  background-color: cornflowerblue;
  color: white;
}

.seat-map {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
}

.seat-map svg {
  display: block;
  margin: auto 0;
  width: 95%;
  height: 60vh;
  outline: solid 1px black;
}

.viewBoxControls .legend {
  margin: 12px;
}

.viewBoxControls button {
  margin: 2px;
}